@import "../../../node_modules/react-alice-carousel/lib/alice-carousel.css";


.admin-voucher-container {
  background: url("/account-bg1.jpg") no-repeat center center/ cover;
  box-shadow: 0 3000px rgba(0, 0, 0, 0.25) inset;
}

.admin-voucher-container::-webkit-scrollbar {
  display: none;
}


.voucher-container1 {
  margin-top: 10vh;
  /* border: 2px solid black; */
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* flex-wrap: wrap; */
}

.voucher {
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  width: 100%;
  /* height: 100px; */
}

h1 {
  width: 100%;
  display: flex;
  justify-content: center;
}

.heading2 {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 10px;
  /* border: 2px solid blue; */
}

.vouchers {
  border: 1px solid grey;
  border-radius: 10px;
  margin: 10px;
  padding: 5px;
  padding-left: 10px;
  width: 35vw;
  min-width: 300px;
  color: white;
  padding: 2vmin;
  background-color: white;
}

.vouchers h2{
  font-size: 3vmin;
}

.voucherdesc,
.vouchercost,
.vouchers p{
  font-size: 2vmin;
}

.voucherdelete{
  font-size: 1.5vmin;
}

.alert {
  color: black;
  border: 2px solid black;
  background-color: beige;
  height: 20%;
  margin-top: 200px;
  width: 40%;
  padding: 20px;
  display: flex;
  align-content: center;
  justify-content: center;
  flex-wrap: wrap;
}
.alertbody {
  background-color: rgba(211, 211, 211, 0.5);
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-content: center;
}
